<template>
	<div>
		<mu-form :model="form" class="mu-demo-form" label-width="100">
			<mu-form-item prop="input" label="Your Name">
				<mu-text-field v-model="form.player"></mu-text-field>
			</mu-form-item>
			<!-- <mu-form-item prop="input" label="Password">
                <mu-text-field v-model="form.password"></mu-text-field>
			</mu-form-item>-->
			<mu-form-item>
				<mu-button color="primary" @click="submit">Submit</mu-button>
			</mu-form-item>
		</mu-form>
	</div>
</template>
<script>
export default {
	data() {
		return {
			form: {}
		}
	},
	methods: {
		submit() {
			sessionStorage.setItem('user', this.form.player)
			this.$router.push('/rooms')
		}
	}
}
</script>
<style lang="less" scoped>
.mu-demo-form {
	margin: 30vh auto;
	width: 350px;
	background: white;
	padding: 20px;
	box-shadow: 0 2px 1px -1px rgba(0, 0, 0, 0.2),
		0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 1px 3px 0 rgba(0, 0, 0, 0.12);
}
</style>


