import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Login from './views/Login.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      redirect: '/login',
      children: [
        {
          path: '/rooms',
          name: 'Rooms',
          component: () => import(/* webpackChunkName: "rooms" */ './views/Rooms.vue'),
        },
        {
          path: '/newGame',
          name: 'New Game',
          component: () => import(/* webpackChunkName: "newGame" */ './views/NewGame.vue'),
        },
        {
          path: '/game',
          name: 'Game',
          component: () => import(/* webpackChunkName: "game" */ './views/Game.vue'),
        }
      ]
    }, {
      path: '/login',
      name: 'WELCOME',
      component: Login
    }
  ]
})
