<template>
	<div class="home">
		<mu-appbar style="width: 100%;" color="primary">
			<mu-button icon slot="left">
				<mu-icon value="games"></mu-icon>
			</mu-button>
			{{$route.name}}
			<mu-menu slot="right" @open="handleOpen">
				<mu-button flat>{{userName}}</mu-button>
				<mu-list slot="content">
					<mu-list-item button>
						<mu-list-item-content>
							<mu-list-item-title>Score: {{score}}</mu-list-item-title>
						</mu-list-item-content>
					</mu-list-item>
					<mu-list-item button @click="open = true">
						<mu-list-item-content>
							<mu-list-item-title>History</mu-list-item-title>
						</mu-list-item-content>
					</mu-list-item>
					<mu-list-item button @click="logout">
						<mu-list-item-content>
							<span>Log out</span>
						</mu-list-item-content>
					</mu-list-item>
				</mu-list>
			</mu-menu>
		</mu-appbar>
		<mu-drawer :open.sync="open" :docked="false" :right="false">
			<mu-list>
				<mu-list-item button v-for="(item,idx) in history" :key="idx">
					<mu-icon v-if="item.win" value="check_circle" color="green"></mu-icon>
					<mu-icon v-else value="close" color="red"></mu-icon>
					<mu-list-item-title>{{item.time | getTime}}</mu-list-item-title>
				</mu-list-item>
			</mu-list>
		</mu-drawer>
		<router-view></router-view>
	</div>
</template>

<script>


export default {
	name: 'home',
	data() {
		return {
			open: false,
			userName: sessionStorage.getItem('user') || 'player',
			history: [],
			score: 0
		}
	},
	mounted() {
		this.getHistory()
	},
	filters: {
		getTime(t) {
			return new Date(t).toLocaleString()
		}
	},
	methods: {
		logout() {
			sessionStorage.clear()
			this.$router.push('/')
		},
		handleOpen() {
			this.getHistory()
		},
		getHistory() {
			this.$http({
				url: '/users',
				method: 'get',
				data: {
					user: sessionStorage.getItem('user')
				}
			}).then(res => {
				this.history = res || []
				this.score = this.history.filter(v => v.win).length
			})
		}
	}
}
</script>
