import Axios from 'axios'

Axios.defaults.timeout = 5000;
Axios.defaults.baseURL = 'http://localhost:3000'



const http = ({ url, data = {}, method = 'get' }) => {
    const m = method.toLowerCase()
    const config = {
        method: m,
        url
    }
    if (m === 'get' || m === 'delete') {
        config.params = data
    } else {
        config.data = data
    }
    return new Promise((resolve, reject) => {
        Axios(config)
            .then(res => {
                if (res.status === 200) {
                    resolve(res.data)
                } else {
                    reject()
                }
            })
            .catch(err => {
                alert('网络错误');
            })
    })
}

export default http
