import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {

  },
  mutations: {
    SOCKET_message(state, msg) {
      const { action, data } = msg
      switch (action) {
        case "socketId":
          sessionStorage.setItem('socketId', data)
          break;

        default:
          break;
      }
    }
  },
  actions: {

  }
})
